import {
  Box,
  CircularProgress,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@mui/material';
import React, { useEffect, useState } from 'react';
import { useAuthAPI } from '../../hooks/authAPI';

export default function EmbeddedResourceTable({
  selectCallback = () => null,
  excludeList = [],
}) {
  const [resourceList, setResourceList] = useState([]);
  const [loading, setIsLoading] = useState(false);

  const authAPI = useAuthAPI();

  const formatResType = (resType) => {
    return resType
      .split('_')
      .map((word) => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase())
      .join(' ');
  };

  const handleRowSelect = async (roleObj = null) => {
    if (selectCallback && roleObj) {
      setIsLoading(true);
      await selectCallback(roleObj);
    }
  };

  useEffect(() => {
    const getResources = async () => {
      setIsLoading(true);
      let data = await authAPI.getResources();
      if (data) {
        const dataFilter = data.filter(
          (resource) =>
            !excludeList.find(
              (ex) =>
                ex.Name === resource.Name &&
                ex.ResType === resource.ResType &&
                ex.Variant === resource.Variant
            )
        );
        setResourceList(dataFilter);
      }
      setIsLoading(false);
    };
    getResources();
  }, []);

  return (
    <>
      {loading ? (
        <Box sx={{ display: 'flex', justifyContent: 'center' }}>
          <CircularProgress />
        </Box>
      ) : resourceList?.length ? (
        <>
          <TableContainer component={Paper}>
            <Table sx={{ minWidth: 300 }} aria-label='Embedded Resource Table'>
              <TableHead>
                <TableRow>
                  <TableCell>Name</TableCell>
                  <TableCell>Variant</TableCell>
                  <TableCell>Type</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {resourceList?.map((row, index) => {
                  return (
                    <TableRow
                      key={index}
                      onClick={() => handleRowSelect(row)}
                      style={{ cursor: 'pointer' }}
                      hover
                    >
                      <TableCell>{row.Name}</TableCell>
                      <TableCell>{row.Variant}</TableCell>
                      <TableCell>{formatResType(row.ResType)}</TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
        </>
      ) : (
        <span>No Existing Resources!</span>
      )}
    </>
  );
}
