import {
  Button,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@mui/material';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import React, { useEffect, useState } from 'react';
import { useAuthAPI } from '../../hooks/authAPI';
import EmbeddedResourceTable from './embeddedResourceTable';

export default function AdminResourceTable({
  selectCallback = () => null,
  userNameProp = '',
  roleNameProp = '',
  groupNameProp = '',
}) {
  const [resourceList, setResourceList] = useState([]);
  const [open, setOpen] = useState(false);
  const authAPI = useAuthAPI();

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const handleRowSelect = async (resourceName = null) => {
    if (selectCallback && resourceName) {
      await selectCallback(resourceName);
      handleClose();
    }
  };

  useEffect(() => {
    const getData = async () => {
      if (!open) {
        let data = [];
        if (userNameProp) {
          data = await authAPI.getUserResources(userNameProp);
        } else if (roleNameProp) {
          data = await authAPI.getRoleResources(roleNameProp);
        } else if (groupNameProp) {
          data = await authAPI.getGroupResources(groupNameProp);
        }

        if (data) {
          setResourceList(data);
        }
      }
    };
    getData();
  }, [open]);

  return (
    <>
      <strong style={{ float: 'left' }}>Resources</strong>
      <Button
        style={{ float: 'right', marginBottom: '0.5em' }}
        onClick={handleClickOpen}
        data-testid='addResourceTestId'
      >
        Add Resource
      </Button>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 300 }} aria-label='Admin Resource Table'>
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
              <TableCell>Variant</TableCell>
              <TableCell>Type</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {resourceList?.map((row, index) => {
              return (
                <TableRow key={index} hover>
                  <TableCell>{row.Name}</TableCell>
                  <TableCell>{row.Variant}</TableCell>
                  <TableCell>{row.ResType}</TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <Dialog
        fullWidth={true}
        maxWidth={'sm'}
        open={open}
        onClose={handleClose}
        aria-labelledby='dialog-title'
        aria-describedby='dialog-description'
      >
        <DialogTitle id='dialog-title'>Select new resource</DialogTitle>
        <DialogContent>
          <EmbeddedResourceTable
            excludeList={resourceList}
            selectCallback={handleRowSelect}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Close</Button>
        </DialogActions>
      </Dialog>
    </>
  );
}
