import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import * as React from 'react';
import { useAuthAPI } from '../../../hooks/authAPI';
import AdminResourceTable from '../resourcesTable';
import AdminRolesTable from '../roleTable';

export default function UserPage({ userName = '' }) {
  const authAPI = useAuthAPI();

  const handleNewRoleSelect = async (roleName = null) => {
    if (userName && roleName) {
      await authAPI.addUserRole(userName, roleName);
    }
  };

  const handleNewResourceSelect = async (resourceObj) => {
    if (userName && resourceObj) {
      await authAPI.addUserResource(
        userName,
        resourceObj.Name,
        resourceObj.Variant,
        resourceObj.ResType
      );
    }
  };

  return (
    <>
      <h2>User: {userName.toUpperCase()}</h2>
      <Box sx={{ flexGrow: 1 }}>
        <Grid container spacing={2}>
          <Grid item xs={6}>
            <AdminRolesTable
              userNameProp={userName}
              embeddedMode
              selectCallback={handleNewRoleSelect}
            />
          </Grid>
          <Grid item xs={6}>
            <AdminResourceTable
              userNameProp={userName}
              selectCallback={handleNewResourceSelect}
            />
          </Grid>
        </Grid>
      </Box>
    </>
  );
}
